                            return 'rgb(255, 190, 122)'; // 5000000-10000000
                        } else if (population >= 10000000 && population < 50000000) {
                            return 'rgb(250, 127, 111)'; // 10000000-50000000
                        } else if (population >= 50000000 && population < 100000000) {
                            return 'rgb(130, 176, 210)'; // 50000000-100000000
                        } else {
